
.comp-full-calendar {
  width: 100%;
}
.reserva {
  position: fixed;
  height: 100vh;
  right: 0;
  top: 0;
  transform: translateX(35vw);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 35vw;
  z-index: 9000;
  background: rgba(255, 255, 255, 0.97);
  padding: 30px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}
.reserva--closed {
    animation: translateX-reverse .4s forwards;
}
.reserva--active {
    animation: translateX .4s forwards;
}
.reserva--column {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
}
.reserva__header {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 40px;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.reserva__title {
    margin: 0 auto;
    font-size: 1.8em;
}
.reserva__close {
    cursor: pointer;
    padding: 4px;
    font-size: 1.8em;
}
.reserva__input-group {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: baseline;
        align-items: baseline;
    border-bottom: 1px solid rgba(50, 50, 50, 0.1);
    margin-bottom: 10px;
}
.reserva__input-group label {
      text-align: right;
      margin-right: 20px;
}
.reserva__input-group input {
      text-align: left;
      padding-left: 0;
      color: #444;
      font-weight: 400;
      cursor: pointer;
}
@keyframes translateX {
0% {
    transform: translateX(35vw);
}
100% {
    transform: translateX(0);
}
}
@keyframes translateX-reverse {
0% {
    transform: translateX(0);
}
100% {
    transform: translateX(35vw);
}
}



