
.full-calendar-header {
  display: -ms-flexbox;
  display: flex;
  height: 40vh;
  background: url("http://images.all-free-download.com/images/graphiclarge/twilight_on_the_sunshine_coast_514662.jpg") center center no-repeat;
  background-size: cover;
  position: relative;
  text-align: center;
  color: #fff;
  font-size: 60px;
  font-weight: 100;
}
.full-calendar-header .month {
    display: -ms-inline-flexbox;
    display: inline-flex;
    padding: 20px 30px;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 100px;
    position: absolute;
    width: 100%;
}
.full-calendar-header .arrows {
    margin: auto 0 0 auto;
    display: block;
}
.full-calendar-header .prev-month,
  .full-calendar-header .next-month {
    cursor: pointer;
    display: inline-block;
    margin-bottom: 10px;
    padding: 10px 30px;
    position: relative;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    transition: 0.5s;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.full-calendar-header .prev-month:hover,
    .full-calendar-header .next-month:hover {
      transform: scale(1.3);
}



