
th {
  text-align: center;
}
.control {
  cursor: pointer;
  padding: 5px 10px;
}
input,
select {
  width: 95%;
  padding: 10px;
}
input:-ms-input-placeholder,
select {
  color: #bbb;
}
input::placeholder,
select {
  color: #bbb;
}
input:-moz-read-only {
  background: transparent;
  border: none;
  text-align: center;
}
input:read-only {
  background: transparent;
  border: none;
  text-align: center;
}
input:-moz-read-only[type="number"] {
    -moz-appearance: textfield;
}
input:read-only[type="number"] {
    -moz-appearance: textfield;
}
input:read-only::-webkit-inner-spin-button, input:read-only::-webkit-outer-spin-button {
    -webkit-appearance: none;
}
.table-create {
  background: #f5f5f5;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  height: 0;
  transform: 1s;
  visibility: hidden;
  display: none;
}
.v-select {
  border: none !important;
  color: none !important;
  background: none !important;
}
.table-create input[type=search] {
  background: #fff !important;
  border: none !important;
}
.table-create input {
  background: #fff;
  border: none;
}
.table-create.opened {
  display: table-row;
  animation: openCreate 0.3s forwards;
}
.table-create.closed {
  display: table-row;
  animation: closeCreate 0.3s forwards;
}
.ti-plus {
  border: 1px solid #000;
  font-size: 25px;
  border-radius: 50%;
  padding: 10px;
  position: absolute;
  right: 0;
  top: 15px;
  cursor: pointer;
}
header, th {
  font-family: Montserrat;
  font-weight: 700;
}
td input {
  font-weight: 400;
}
@keyframes openCreate {
0% {
    height: 0;
    opacity: 0;
    visibility: hidden;
}
100% {
    height: 100px;
    opacity: 1;
    visibility: visible;
}
}
@keyframes closeCreate {
0% {
    height: 100px;
    opacity: 1;
    visibility: visible;
}
100% {
    height: 0;
    opacity: 0;
    visibility: hidden;
}
}



